import React from 'react';
import { useSelector } from 'react-redux';
import Button from '../../../components/ui/Button';
import { getDiscovery } from '../../../store/slices/discovery.slice';
import useBEM from '../../../utils/hooks/useBEM';

interface Props {
    onClose: () => void
}

const DiscoveryLensInfo : React.FC<Props>  = ({
    onClose
}) =>  {
    // VARIABLES
    const [B, E] = useBEM('discovery-lens-info');
    const { lensDescription } = useSelector(getDiscovery);

    // HOOKS

    // METHODS
    const close = () => {
        onClose();
    }

    return (
        <div className={B( lensDescription && lensDescription.length > 0 ? 'show' : '')}>
            
            <Button text="Close" type="simple" click={close}/>

            <div className={E('description')}>
                {/* TODO: put this data on constants */}
                <strong>Correct your vision</strong>                
                <p>Essilor’s offer includes single vision lenses that provide clear vision for seeing things close up or far away for today’s connected digital life. Progressive lenses treat both near and farsightedness together in one single lens for effortless sharp vision at any distance.</p>

                <strong>Protect your eyes</strong>
                <p>Our eyes have precious limited reserves allowing visual and non-visual functions that we call the Eye Capital, that could be impacted by overexposure to UV and blue-violet light among other factors. Light is vital, it can be selectively filtered with adapted lenses to help preserve your Eye Capital, to ensure that light works in our favor rather than against us.</p>
                
                <strong>Enhance clarity</strong>
                <p>Get rid of your everyday enemies such as reflections, scratches, smudges, dust, water, fog, whatever comes your way. Essilor’s technologies make it possible to achieve optimal lens clarity.</p>
            </div>

        </div>
    )
}

export default DiscoveryLensInfo;